<script>
import { nftInfo, receivePre, receive } from '@/api'
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import Nft from '@/components/Nft'
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import MyDialog from "@/components/MyDialog";
import { config } from "@/utils/config";
import idoAbi from "@/utils/idoAbi";
import nftAbi from "@/utils/nftAbi";
import nftBonusAbi from "@/utils/nftBonusAbi";
import {
  Toast
} from "vant";
import { async } from 'q';
import BigNumber from 'bignumber.js';
export default {
  components: {
    Header,
    Footer,
    Nft,
    MyDialog
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      dataImage: [{ image: require('@/assets/images/nftbanner.png') }],
      nftImage: {
        "1": { image: require('@/assets/images/mercury.png') },
        "2": { image: require('@/assets/images/mars.png') },
        "3": { image: require('@/assets/images/venus.png') },
      },
      mynft: [],
      isRefreshNft: false,
      dataNftInfo: {},
      nftContract: null,
      idoContract: null,
      nftBonusContract: null,
      dialogRecive: {
        show: false,
        received: '',
        balance: ''
      },
      reciveBtn: {
        loading: false
      },
      initContract: () => {
        data.nftContract = new data.web3.eth.Contract(
          nftAbi,
          config.NFT
        );
        data.idoContract = new data.web3.eth.Contract(
          idoAbi,
          config.IDO
        );
        data.nftBonusContract = new data.web3.eth.Contract(
          nftBonusAbi,
          config.NFTBONUS
        );
        data.initData()
      },
      initData: () => {
        data.nftInfoHandle()
      },
      nftInfoHandle: async () => {
        const Toast0 = Toast.loading({
          duration: 0,
        });
        let totalSupply = await data.nftContract.methods
          .totalSupply()
          .call({ from: data.address });
        data.dataNftInfo.total_nft = totalSupply;
        console.log(totalSupply, 'totalSupply');
        let totalDividendsDistributed = await data.nftBonusContract.methods
          .totalDividendsDistributed()
          .call({ from: data.address });
        data.dataNftInfo.total_nft_share = new BigNumber(totalDividendsDistributed)
          .dividedBy(10 ** 9)
          .toFixed(2);
        let withdrawableDividend = await data.nftBonusContract.methods
          .withdrawableDividendOf(data.address)
          .call({ from: data.address });
        console.log(withdrawableDividend, "withdrawableDividend");
        withdrawableDividend = new BigNumber(withdrawableDividend).dividedBy(10 ** 9).toFixed(2);
        data.dataNftInfo.getting_share = withdrawableDividend;

        let accumulativeDividendOf = await data.nftBonusContract.methods
          .accumulativeDividendOf(data.address)
          .call({ from: data.address });
        accumulativeDividendOf = new BigNumber(accumulativeDividendOf).dividedBy(10 ** 9).toFixed(2);
        console.log(accumulativeDividendOf, "accumulativeDividendOf");
        data.dataNftInfo.getted_share = accumulativeDividendOf - withdrawableDividend;
        data.dialogRecive.balance = withdrawableDividend;
        data.dialogRecive.received = data.dataNftInfo.got;

        let allTokens = await data.nftContract.methods
          .getUserAllTokens(data.address)
          .call({ from: data.address });
        allTokens = [1];
        for (let k = 0; k < allTokens.length; k++) {
          let tokenid = allTokens[k];
          let tokenInfo = await data.idoContract.methods
            .getTokenInfo(tokenid)
            .call({ from: data.address });
          data.mynft.push(tokenInfo);
          console.log(data.mynft, "mynft");
        }


      },
      recivePreHandle: () => {
        const Toast1 = Toast.loading({
          duration: 0,
        });
        data.dialogRecive.show = true
        Toast1.clear()
      },
      reciveHandle: async () => {
        try {
          data.reciveBtn.loading = true
          let _res = await data.nftBonusContract.methods
            .claim()
            .estimateGas({
              from: data.address
            });
          console.log(_res, 'claim estimateGas')
          await data.nftBonusContract.methods.claim().send({
            from: data.address
          })
          data.reciveBtn.loading = false
          Toast(t('index.Success'))
        } catch (error) {
          data.reciveBtn.loading = false
          proxy.$errorformat(error)
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
